body {
    background-color: black;
    font-weight: normal;
    font-family: 'DM Sans', sans-serif;
}

.canvas {
    position: fixed;
    top: 82px;
    bottom: 0px;
    left: 149px;
    right: 0px;

    font-weight: normal;
    font-family: 'DM Sans', sans-serif;
    color: #ababab;
    z-index: -2;
}

.canvas .cover { position: fixed; left: 150px; }
.canvas h1 { font-size: 50px; margin-left: 104px; z-index: 100; color: white;}

.content {
    margin-top: 115px;
    margin-left: 105px;
    height: 565px;
    width: 1000px;
    z-index: -1;
}

.content h2 { font-size: 20px; }
.content h3 { font-size: 15px; }
.content h4 { font-size: 10px; }
.content a { text-decoration: none; }
.content .highlight { color: #6d38ff; }