.about .content {
    margin-top: 7%;
}

.about .content p {
    font-size: 15px;
    margin-left: 3%;
    margin-right: 20%;
}

.about #header {
    color: white;
    font-size: 24px;
    margin-right: 0%;
}

.about .profile {
    filter: brightness(.85);
    -webkit-filter: brightness(.85);
    border-top-left-radius: 50%;
    border-top-right-radius: 50%;
    border-bottom-left-radius: 50%;
    height: 100%;
    opacity: 0.8;
    position: absolute;
    bottom: -200px;
    right: 0px;
}