.homecanvas {
    margin-left: 105px;
    position:fixed;
    width:100%;
    height:100%;
    top:0px;
    left:0px;
    z-index:1000;
    font-family: 'DM Sans', sans-serif;
    background-color: black;
}

a {
    text-decoration: none; /* no underline */
}

.homecanvas h2 {
    font-weight: normal;
    font-size: 20px;
    color: #ababab;
}

.homecanvas h3 {
    font-size: 10px;
}

.fixedcatch {
    position: absolute;
    margin-top: 178px;
    margin-left: 151px;
}

.variablecatch {
    color: white;
    position: absolute;
    margin-top: 178px;
    margin-left: 312px;
}

.highlight {
    color: #6d38ff;
}

.variablecatch span{
    position: absolute;
    opacity: 0;
    overflow: hidden;
    -webkit-animation: rotateWord 10.8s linear infinite 0s;
    -ms-animation: rotateWord 10.8s linear infinite 0s;
    animation: rotateWord 10.8s linear infinite 0s;
}
.variablecatch span:nth-child(2) { 
    -webkit-animation-delay: 3.6s; 
    -ms-animation-delay: 3.6s; 
    animation-delay: 3.6s; 
}
.variablecatch span:nth-child(3) { 
    -webkit-animation-delay: 7.2s; 
    -ms-animation-delay: 7.2s; 
    animation-delay: 7.2s; 
}

@-webkit-keyframes rotateWord {
    0% { opacity: 0; -webkit-transform: translateX(0px); }
    3% { opacity: 1; -webkit-transform: translateX(0px); }
    21% { opacity: 1; -webkit-transform: translateX(0px); }
    24% { opacity: 0; -webkit-transform: translateX(0px); }
    80% { opacity: 0; }
    100% { opacity: 0; }
}
@-ms-keyframes rotateWord {
    0% { opacity: 0; -ms-transform: translateX(0px); }
    3% { opacity: 1; -ms-transform: translateX(0px); }
    21% { opacity: 1; -ms-transform: translateX(0px); }
    24% { opacity: 0; -ms-transform: translateX(0px); }
    80% { opacity: 0; }
    100% { opacity: 0; }
}
@keyframes rotateWord {
    0% { opacity: 0; -webkit-transform: translateX(0px); transform: translateX(0px); }
    3% { opacity: 1; -webkit-transform: translateX(0px); transform: translateX(0px); }
    21% { opacity: 1; -webkit-transform: translateX(0px); transform: translateX(0px); }
    24% { opacity: 0; -webkit-transform: translateX(00px); transform: translateX(0px); }
    80% { opacity: 0; }
    100% { opacity: 0; }
}

.cover {
    position: absolute;
    margin-top: 135px;
}

.captionbox {
    position: absolute;
    margin-top: 550px;
    width: 100%;
    height: 150px;
}

.caption {
    color: black;
    margin-top: 150px;
    margin-left: 149px;
}

.caption .captioni {
    color: black;
    transition: all .8s ease 1.2s;
    -o-transition: all .8s ease 1.2s;
    -moz-transition: all .8s ease 1.2s;
    -webkit-transition: all .8s ease 1.2s;
}

.captionbox:hover .captioni {
    color: #ababab;
    transition: all .8s ease;
    -o-transition: all .8s ease;
    -moz-transition: all .8s ease;
    -webkit-transition: all .8s ease;
}

.caption .captioni a {
    color: black;
    transition: all .8s ease 1.2s;
    -o-transition: all .8s ease 1.2s;
    -moz-transition: all .8s ease 1.2s;
    -webkit-transition: all .8s ease 1.2s;
}

.captionbox:hover a {
    color: #ababab;
    transition: all .8s ease;
    -o-transition: all .8s ease;
    -moz-transition: all .8s ease;
    -webkit-transition: all .8s ease;
}

.captiont a {
    font-style: italic;
}