.projects .content {
    margin-top: 7%;
    /* margin-left: 95px; */
    width: 1100px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}
    .projects .responsiveIcon {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-content: center;

        z-index: 0;
        height: 90px;
        width: 90px;
        padding: 3px;
        border-radius: 50%;
        opacity: 1;

        background-size: 100% 100%;
        transition: border .3s opacity 0.3;
        border: 5px solid rgb(37, 37, 37);
    }
    .projects .icon {
        z-index: 100;
        max-height: 80px;
        max-width: 80px;
        border-radius: 50%;
        display: block;
        padding: 7px;
        background-color: black;
    }

    .responsiveIcon:hover {
        opacity: 0.7;
        border: 5px solid rgb(109, 56, 255);
    }

.content .right {
    width: 45%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
    .content .right .project {
        height: 25%;
        margin-top: 6%;
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;
    }

.content .left {
    margin-left: 3%;
    width: 45%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
    .content .left .project {
        height: 25%;
        margin-top: 6%;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
    }

#noltop, #nortop {
    margin-top: 0%;
}