.Sidebar {
    position: fixed;
    height: 100%;
    width: 105px;
    background-color: black;
    
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    font-family: 'DM Sans', sans-serif;
}

.socials {
    padding: 20px;
    width: 40px;
}

.spacer1 {
    opacity: 0%;
    height: 356px;
    width: 40px;
}

.spacer2 {
    opacity: 0%;
    height: 33px;
    width: 40px;
}

.linkbox {
    opacity: 1;
    width: 89px;
    border-left: 8px solid black;
    border-right: 8px solid black;
}

.linkbox:hover {
    opacity: 0.8;
    border-left: 8px solid rgb(109, 56, 255);
}

.link {
    margin-left: 15px;
    margin-right: 15px;
    font-size: 14px;
    padding-top: 9px;
    padding-bottom: 9px;
    border-top: 1px solid #4e4e4e;
}

.linkbox a {
    text-decoration: none;
    color: #fff;
}